import React from 'react'
import PersonIcon from 'material-ui/svg-icons/action/account-circle'
import {Toolbar, ToolbarTitle} from 'material-ui/Toolbar'
import IconButton from 'material-ui/IconButton'

const TopBar = (props) => {

    const iconStyles = {height: 50, width: 50, marginTop: -10},
        onIconClicked = () => props.viewDialog(); // notify the parent

    return (
        <Toolbar style={{backgroundColor: '#D52B1E', color: 'white', marginBottom: 20}}>
            <ToolbarTitle text="Chat App"/>
            <IconButton tooltip="View Logged In Users"
                        tooltipPosition="bottom-left"
                        onClick={onIconClicked}
                        iconStyle={iconStyles}
            >
                <PersonIcon style={iconStyles} color='white'/>
            </IconButton>
        </Toolbar>
    )
};

export default TopBar


